import React, { Component } from "react";
import _ from "lodash";
import { connect } from "react-redux";
import { fetchClientes } from "../actions/index";

import ItemCliente from "./ItemCliente";

class ListaClientes extends Component {
    componentWillMount() {
        this.props.fetchClientes();
    }

    renderClientes() {
        return _.map(this.props.clientes, clientes => {
            return <ItemCliente key={clientes.IdCiente} clientes={clientes} />;
        });
    }

    render() {
        return (
            <div className="container">
                <div className="lista">
                    <ul className="list-group list-group-flush col-12" id="list-tab" role="tablist">
                        <li>{this.renderClientes()}</li>
                    </ul>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return { clientes: state.clientes };
}

export default connect(
    mapStateToProps,
    { fetchClientes }
)(ListaClientes);
